@import url('https://fonts.googleapis.com/css2?family=Akaya+Telivigala&family=Poppins:wght@200&display=swap');
*,
*::before,
*::after{
    padding: 0;
    margin:0;
    box-sizing: border-box;
    outline: none;
    text-decoration: none;
}

$bg_black_900: #6f7b86;
// $bg_black_100: #232d3b;
// $bg_black_50: #5f6670;
$bg_black_100: #16171a;
$bg_black_50: #1c2736;
$text_black_900: #bbbfc4;
$text_black_700: #e5ebf7;
// $skin_color: #0b64d8;
$skin_second_color: #188dfaf5;
$skin_color: #2aa8f1;

body{
    line-height: 1.5;
    background: url(../assets//img/bg.jpg) top right no-repeat fixed;
    background-size: cover;
    transition: all .3s ease;
}
ul{
    list-style: none;
    margin-top: 60px;
}
.sections{
    // background: blue;
    padding-left: 10rem;
    min-height: 100vh;
    display: block;
    z-index: 0;
    opacity: 1;
    transition: all .3s ease;
}

.padd-15{
    padding: 0 2%;
}

.btn{ 
    font-size: 16px;
    font-weight: 500;
    padding: 6px 25px;
    background: transparent;
    color: white;
    border-radius: 40px;
    box-shadow: inset 5px 5px 16px #1b1e20,
                inset -5px -5px 10px #21374d;
    display: inline-block;
    white-space: nowrap;
    transition: all 0.3s ease;
    outline: none;
}
.btn:hover{
    transform: scale(1.05);
}

.flex{
    display:flex;
}
.flex-d{
    flex-direction: column;
}
.grid{
    display: grid;
}

.m{
    margin-top: 30px;
}
.ml{
    margin-left: .5rem;
}
.mr{
    margin-right: .5rem;
}

.shadow-dark{
    background: #232d3b;
    box-shadow:  5px 5px 10px #161c25,
                -5px -5px 10px #303e51;
}
.shadowbg_50{
    background: #5f6670;
    box-shadow:  3px 3px 6px #3b3f45,
                -3px -3px 6px #838d9b;
}
.shadow_text{
    border-radius: 62px;
    background: #1c6bd1;
    box-shadow: inset 18px 18px 27px #114282,
                inset -18px -18px 27px #2794ff;
}

.hidden{
    display: none;
}

.name{
    padding: 1rem 0;
    display: flex;
    align-items: center;

    p{
        padding-right: 0.4rem;
        text-transform: uppercase;
        color: $text_black_700;
    }

    .dash{
        background: $skin_color;
        width: 8rem;
        height: 2.5px;
        border-radius: 3px;
    }
}

@media only screen and (max-width: 520px) {
    .padd-15{
        padding: 0 1%;
    }
}
@media only screen and (max-width: 680px) {
    .sections{
        padding-left: 0;
    }
}