@import '_config';
@import '_medias';

.menu_toggle{
    cursor: pointer;
    width: 40px;
    height: 20px;
    display: none;
    justify-content: center;
    align-items: center;
    background: transparent;
    position: fixed;
    right: 1rem;
    top: 10px;

    span{
        height: 2px;
        width: 18px;
        border-radius: 3px;
        background: $text_black_900;
        display: inline-block;
        position: relative;
        transition: all .3s ease;
    }
    span::before{
        content: "";
        height: 1.7px;
        width: 20px;
        border-radius: 3px;
        background: $text_black_900;
        display: inline-block;
        position: absolute;
        top: -6px;
        right: 0;
        transition: all .3s ease;
    }
    span::after{
        content: "";
        height: 1.7px;
        width: 10px;
        border-radius: 3px;
        background: $text_black_900;
        position: absolute;
        bottom: -6px;
        right: 0;
        transition: all .3s ease;
    }
    
}
.open.menu_toggle{
    span{
        transform: translate(-8px);
         visibility: hidden;
         transition: .01s ease;
     }
     span::before{
         content: "";
         height: 1.7px;
         width: 20px;
         border-radius: 3px;
         background: $text_black_900;
         display: block;
         transform: rotate(-45deg);
         transform-origin: right;
         position: absolute;
         top: -6.5px;
         right: -6px;
         opacity: 1;
         transition: .3s ease;
         visibility: visible;
         
     }
     span::after{ 
         content: "";
         height: 1.7px;
         width: 20px;
         border-radius: 3px;
         background: $text_black_900;
         display: block;
         position: absolute;
         transform: rotate(45deg);
         transform-origin: right;
         top: 7px;
         right: -6px;
         opacity: 1;
         transition: .3s ease;
         visibility: visible;
         
     }
}
.aside{ 
    font-family: 'Courier New', Courier, monospace;
    width: 10rem;
    min-height: 100vh;
    background: $bg_black_100;
    box-shadow:  3px 3px 8px #161c25,
                    -3px -3px 8px #303e51;
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    transition: all .3s ease;
    
    .logo{
        position: absolute;
        top: 50px;
        font-size: 2rem;
        margin-bottom: 2rem;

        a{
            color: $text_black_900;
            font-weight: 700;
            padding: 10px 20px;
            font-size: 2rem;
            letter-spacing: 5px;
            font-family: 'Akaya Telivigala', cursive;

            span{
                font-style: italic;
                font-weight: 900;
            }
        }

        a::before{
            content: "";
            position: absolute;
            width: 20px;
            height: 20px;
            border-bottom: 5px solid $skin_color;
            border-left: 5px solid $skin_color;
            border-radius: 40%;
            bottom: 0;
            left: 0;
        }
        a::after{
            content: "";
            position: absolute;
            width: 20px;
            height: 20px;
            border-top: 5px solid $skin_color;
            border-right: 5px solid $skin_color;
            border-radius: 40%;
            top: 0;
            right: 0;
        }
    }

    li{
        margin-bottom: 1rem;
        display: block;
        border-radius: 7px;
        box-shadow:  5px 5px 10px #161c25,
                -5px -5px 10px #303e51;
        a{
            font-size: .9rem;
            font-weight: 600;
            display: block;
            padding: 5px 10px;
            color: $text_black_900;

            .icon{
                padding-right: 7px;
            }
        }

        a.active{
            color: $skin_second_color;
        }
    }
}

.main_content{
    // background: rgba(173, 172, 172, 0.5);
    section{
        padding: 0 20px;
        .containers{
            background: #16171ab7;
            padding: 40px 0;
            max-width:1100px;
            width: 100%; 
            margin: auto; 

            // .row.container{
    
            // }
        }
    }
    // Home
    .home{
        // display: none;
        margin: 0 auto;
        min-height: 100vh;
        max-height: 100vh;
        color: $text_black_900;

        .link{
            display: block;
            border-radius: 7px;
            box-shadow:  2px 2px 5px #13181f,
                    -2px -2px 5px #262b33;
            font-size: 1.3rem;
            font-weight: 600;
            display: block;
            padding: 5px 20px;
            color: $text_black_900;
            width: 10rem;
            text-align: center;
        }

        

        .container{
            padding: 5rem 0;
            max-width:1100px;
            width: 100%; 
            margin: auto; 
            height: 100vh; 
            
            .row{
                margin: 0 -15px;
                position: relative;
    
                .home-info{
                    flex: 0 0 80%;
                    max-width: 80%;
    
                    .hello{
                        font-size: 28px;
                        margin: 15px 0;
                        color: white;
                        span{
                            font-family: 'Akaya Telivigala', cursive;
                            font-size: 30px;
                            font-weight: 700;
                            color: $skin_color;
                        }
                    }
                    .my-profession{
                        font-size: 30px;
                        margin: 15px 0;
                        color: white;
    
                        .typing{
                            font-family: 'Akaya Telivigala', cursive;
                            color: $skin_color;
                        }
                    }
                    p{
                        margin-bottom: 70px;
                        font-size: 20px;
                        color: white;
                    }
                }
        
                // .home-img{
                //     flex: 0 0 40%;
                //     max-width: 40%;
                //     text-align: center;
                //     position: relative;
            
                //     img{
                //         width: 100%;
                //         // height: 100%;
                //     }
                // }
            }
        }
    }
    // Home

    // About
    .section-title{
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 40px;

        h2{
            font-size: 40px;
            color: $text_black_900;
            font-weight: 700;
            position: relative;
        }

        h2::before{
            content: "";
            height: 4px;
            width: 50px;
            background: $skin_color;
            position: absolute;
            top: 100%;
            left: 0;
        }
        h2::after{
            content: "";
            height: 4px;
            width: 25px;
            background: $skin_color;
            position: absolute;
            top: 100%;
            left: 0;
            margin-top: 8px;
        }
    }

    .about-content{
        flex: 0 0 100%;
        max-width: 100%;
        margin-bottom: 20px;

        .about-text{
            flex: 0 0 100%;
            max-width: 100%;

            h3{
                font-size: 24px;
                margin-bottom: 15px;
                font-weight: 700;
                color: $text_black_900;

                span{
                    color: $skin_color
                }
            }
            p{
                font-size: 16px;
                line-height: 25px;
                color: $text_black_700;
            }
        }

        .row.flex.m{
            flex-direction: column;
        }
        .personal-info{
            flex: 0 0 100%;
            max-width: 100%;
            .grid{
                grid-template-columns: 1fr;
            }

            .info-item{
                flex: 0 0 100%;
                max-width: 100%;                
                p{
                    padding: 10px 0;
                    
                    .firstSpan{
                        font-size: 14px;
                        font-weight: 600;
                        color: $text_black_900;
                    }
                }
                .icon{
                    color: $skin_color;
                    font-size: 10px;
                }

                span{
                    padding-left: 3px;
                    font-size: 13px;
                    font-weight: 400;
                    color: $text_black_700;
                    display: inline-block;
                }
            }
            .buttons{
                margin-top: 20px;
                .btn{
                    margin-right: 10px;
                    margin-top: 10px;
                }
            }
        }
        .skills{
            flex: 0 0 100%;
            max-width: 100%;
            margin-bottom: 3rem;

            .skill-item{
                flex: 0 0 100%;
                max-width: 100%;
                margin-bottom: .7rem;
                // background-color: red;

                h5{
                    line-height: 30px;
                    font-weight: 600;
                    font-size: 16px;
                    color: $text_black_900;
                    text-transform: capitalize;
                }
                .skill-percent{
                    position: absolute;
                    right: 0;
                    color: $text_black_900;
                    top: -35px;
                    font-weight: 400;
                    line-height: 40px;
                }
                .progress{
                    background-color: $bg_black_50;
                    height: 3px;
                    width: 100%;
                    border-radius: 4px;
                    position: relative;

                    .progress-in{
                        position: absolute;
                        left: 0;
                        top: 0;
                        height: 100%;
                        border-radius: 4px;
                        background: $skin_color;
                    }
                }
            }
        
        }

    }

    // About

    // Resume

    .resume{
        .row.flex.container{
            flex-direction: column;
        }
        .education,
        .experience{
            flex: 0 0 100%;
            max-width: 100%;

            .title{
                font-size: 24px;
                margin-bottom: 30px;
                font-weight: 700;
                color: $text_black_900;
            }

            .timeline-box{
                flex: 0 0 100%;
                max-width: 100%;
            }

            .timeline{
                background: $bg_black_100;
                padding: 30px 15px;
                border: 1px solid $bg_black_50;
                border-radius: 10px;
                width: 100%;
                position: relative;

                .timeline-item{
                    position: relative;
                    padding-left: 37px;
                    padding-bottom: 50px;

                    .timeline-date{
                        font-weight: 400;
                        font-size: 14px;
                        margin-bottom: 12px;
                        color: $text_black_700;

                        .icon{
                            margin-right: 5px;
                        }
                    }
                    .timeline-title{
                        font-weight: 700;
                        font-size: 18px;
                        margin-bottom: 15px;
                        color: $text_black_900;
                    }
                    p{
                        font-size: 12px;
                        line-height: 20px;
                        color: $text_black_700;
                        text-align: justify;
                    }
                }
                .timeline-item:last-child{
                    padding-bottom: 0;
                }

                .timeline-item::before{
                    content: "";
                    width: 1px;
                    position: absolute;
                    height: 100%;
                    left: 7px;
                    top: 0;
                    background-color: $skin_color;
                }
                .circle-dot{
                    position: absolute;
                    left: 0;
                    top: 0;
                    height: 15px;
                    width: 15px;
                    border-radius: 50%;
                    background: $skin_color;
                }
               
            }
        }
    }
    // Resume

    // Services
    .services{
        .container{
            padding-bottom: 40px;

            // .container{
                //  padding: 30px 30px;
            // }
        }
        .service-item{
            // padding:0 40px;
            grid-template-columns: repeat(1, 1fr);
            grid-row-gap: 2rem;

            .service-item-inner{
                background: $bg_black_100;
                border: 1px solid $bg_black_50;
                border-radius: 10px;
                padding: 30px 15px;
                text-align: center;
                transition: all 0.3s ease;

                .icon-container{
                    height: 60px;
                    width: 60px;
                    border-radius: 50%;
                    display: block;
                    margin: 0 auto 30px;
                    transition: all 0.3s ease;

                    .icon{
                        font-size: 40px;
                        margin-top: 10px;
                        color: $skin_color;
                    }
                }

                h4{
                    font-size: 18px;
                    margin-bottom: 15px;
                    color: $text_black_900;
                    text-transform: capitalize;
                    font-weight: 700;
                }
                p{
                    font-size: 15px;
                    color: $text_black_700;
                    line-height: 25px;
                }
            } 
            .service-item-inner:hover{
                box-shadow: 0 0 20px rgba(48, 46, 77, 0.15);

                .icon-container{
                    background-color: $skin_color;

                    .icon{
                     color: $bg_black_50;
                    }
                }
            }
        }
    }
    // Services

    // Portfolio
    .portfolio{
        .portfolio-heading{
            flex: 0 0 100%;
            max-width: 100%;
            margin-bottom: 40px;

            h2{
                color: $text_black_900;
                font-weight: 500;
            }
        }
        .grid{
            grid-template-columns: repeat(1, 1fr);
            grid-row-gap: 2rem;
        }
        .portfolio-item{
            width: 100%;
        }
            .portfolio-item-inner{
                border: 5px solid $bg_black_100;
                border-radius: 10px;
                overflow: hidden;
                cursor: pointer;
                width: 100%;

                img{
                    width: 100%;
                    display: block;
                }
            }
        
    }
    // Portfolio

    // Comtact
    .contact{
        .contact-title{
            color: $skin_color;
            text-align: center;
            font-size: 25px;
            margin-bottom: 20px;
        }
        .contact-sub-title{
            color: $text_black_900;
            text-align: center;
            font-size: 15px;
            margin-bottom: 50px;
        }
        .row.padd-15.grid{
            grid-template-columns: repeat(4, 1fr);
        }
        .contact-info-item{
            // flex: 0 0 25%;
            // max-width: 25%;
            text-align: center;

            .icon-container{
                display: inline-block;
                
                .icon{
                    font-size: 25px;
                    color: $skin_color;
                }
            }

            h4{
                font-size: 18px;
                font-weight: 700;
                color: $text_black_900;
                text-transform: capitalize;
                margin: 15px 0;
            }
            p{
                font-size: 16px;
                line-height: 25px;
                color: $text_black_700;
                font-weight: 400;
            }
        }

        .contact-form{
            flex: 0 0 100%;
            max-width: 100%;

            .grid{
                grid-template-columns: repeat(1, 1fr);
                grid-column-gap: 1rem;
            }

            .col-6{
                max-width: 100%;
            }
            .col-12{
                flex: 0 0 100%;
                max-width: 100%;
            }
            .form-item{
                margin-bottom: 30px;

                .form-control{
                    width: 100%;
                    height: 2.4rem;
                    border-radius: 4px;
                    background: $bg_black_100;
                    border: 1px solid $bg_black_50;
                    padding: .2rem .5rem;
                    font-size: 16px;
                    color: $text_black_700;
                    outline: none;
                    transition: all .3s ease;
                }
                .form-control:focus{
                    box-shadow:  5px 5px 10px #161c25,
                    -5px -5px 10px #303e51;
                }

                .btn{
                    height: 3rem;
                    padding:0 3rem;
                    border: none;
                }
                textarea{
                    resize: none;
                    height: 5rem;
                    width: 100%;
                }
            }
        }
    }
    // Contact
    
}

@media only screen and (max-width: 798px) {
    
    .main_content{
        section .containers{
            max-width:35rem;
            width: 100%;
            margin: auto;
        }
        .services {
            .service-item{
                grid-template-columns: repeat(2, 1fr);
                grid-column-gap: 1rem;
            }
        }
        .contact {
            .row.padd-15.grid{
                grid-template-columns: repeat(2, 1fr);
            }
            .contact-form{

                .row.flex{
                    flex-direction: row;
                }
            }
        }

        .portfolio{
            .grid{
                grid-template-columns: repeat(2, 1fr);
            }
        }
    }
    
}
@media only screen and (max-width: 520px) {
    .main_content {
        .services {
            .service-item{
                grid-template-columns: repeat(1, 1fr);
            }
        }
        .portfolio{
            .grid{
                grid-template-columns: repeat(1, 1fr);
            }
        }
        .contact {
            .row.padd-15.grid{
                grid-template-columns: repeat(1, 1fr);
            }     
        }
    }
}

@media only screen and (min-width: 798px) {
    .main_content{
        section .containers{
            max-width:45rem;
            width: 100%;
            margin: auto;
        }
        .about-content .row.flex.m{
            flex-direction: row;

            .skills,
            .personal-info{
                flex: 0 0 50%;
                max-width: 50%;
            }
        }
        .resume{
            .row.flex.container{
                flex-direction: row;
            }
            .education,
            .experience{
                flex: 0 0 50%;
                max-width: 50%;
            }
        }
        .services {
            .service-item{
                grid-template-columns: repeat(3, 1fr);
                grid-column-gap: 1rem;
            }
        }
        .contact{
             .contact-form{
                .row.grid{
                    grid-template-columns: 1fr 1fr;
                }
                
            }
        }

        .portfolio{
            .grid{
                grid-template-columns: repeat(3, 1fr);
            }
        }
    }
}
@media only screen and (max-width: 680px) {
    .menu_toggle{
        display: flex;
    }
    .aside{
        left: -11rem;
    }
}