.menu_toggle {
  cursor: pointer;
  width: 40px;
  height: 20px;
  display: none;
  justify-content: center;
  align-items: center;
  background: transparent;
  position: fixed;
  right: 1rem;
  top: 10px;
}
.menu_toggle span {
  height: 2px;
  width: 18px;
  border-radius: 3px;
  background: #bbbfc4;
  display: inline-block;
  position: relative;
  transition: all 0.3s ease;
}
.menu_toggle span::before {
  content: "";
  height: 1.7px;
  width: 20px;
  border-radius: 3px;
  background: #bbbfc4;
  display: inline-block;
  position: absolute;
  top: -6px;
  right: 0;
  transition: all 0.3s ease;
}
.menu_toggle span::after {
  content: "";
  height: 1.7px;
  width: 10px;
  border-radius: 3px;
  background: #bbbfc4;
  position: absolute;
  bottom: -6px;
  right: 0;
  transition: all 0.3s ease;
}

.open.menu_toggle span {
  transform: translate(-8px);
  visibility: hidden;
  transition: 0.01s ease;
}
.open.menu_toggle span::before {
  content: "";
  height: 1.7px;
  width: 20px;
  border-radius: 3px;
  background: #bbbfc4;
  display: block;
  transform: rotate(-45deg);
  transform-origin: right;
  position: absolute;
  top: -6.5px;
  right: -6px;
  opacity: 1;
  transition: 0.3s ease;
  visibility: visible;
}
.open.menu_toggle span::after {
  content: "";
  height: 1.7px;
  width: 20px;
  border-radius: 3px;
  background: #bbbfc4;
  display: block;
  position: absolute;
  transform: rotate(45deg);
  transform-origin: right;
  top: 7px;
  right: -6px;
  opacity: 1;
  transition: 0.3s ease;
  visibility: visible;
}

.aside {
  font-family: "Courier New", Courier, monospace;
  width: 10rem;
  min-height: 100vh;
  background: #16171a;
  box-shadow: 3px 3px 8px #161c25, -3px -3px 8px #303e51;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
  transition: all 0.3s ease;
}
.aside .logo {
  position: absolute;
  top: 50px;
  font-size: 2rem;
  margin-bottom: 2rem;
}
.aside .logo a {
  color: #bbbfc4;
  font-weight: 700;
  padding: 10px 20px;
  font-size: 2rem;
  letter-spacing: 5px;
  font-family: "Akaya Telivigala", cursive;
}
.aside .logo a span {
  font-style: italic;
  font-weight: 900;
}
.aside .logo a::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-bottom: 5px solid #2aa8f1;
  border-left: 5px solid #2aa8f1;
  border-radius: 40%;
  bottom: 0;
  left: 0;
}
.aside .logo a::after {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-top: 5px solid #2aa8f1;
  border-right: 5px solid #2aa8f1;
  border-radius: 40%;
  top: 0;
  right: 0;
}
.aside li {
  margin-bottom: 1rem;
  display: block;
  border-radius: 7px;
  box-shadow: 5px 5px 10px #161c25, -5px -5px 10px #303e51;
}
.aside li a {
  font-size: 0.9rem;
  font-weight: 600;
  display: block;
  padding: 5px 10px;
  color: #bbbfc4;
}
.aside li a .icon {
  padding-right: 7px;
}
.aside li a.active {
  color: rgba(24, 141, 250, 0.9607843137);
}

