  .main_content .about-content .row.flex.m {
    flex-direction: row;
  }
  .main_content .about-content .row.flex.m .skills,
  .main_content .about-content .row.flex.m .personal-info {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .main_content .resume .row.flex.container {
    flex-direction: row;
  }
  .main_content .resume .education,
  .main_content .resume .experience {
    flex: 0 0 50%;
    max-width: 50%;
  }
  .main_content .services .service-item {
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 1rem;
  }
  .main_content .contact .contact-form .row.grid {
    grid-template-columns: 1fr 1fr;
  }
  .main_content .portfolio .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media only screen and (max-width: 680px) {
  .menu_toggle {
    display: flex;
  }
  .aside {
    left: -11rem;
  }
}